.hero-banner {
  margin-bottom: 4rem;
}

.hero-banner_container {
  padding: 2rem 0;
  box-sizing: border-box;
}

.hero-banner-content {
  margin-bottom: 2rem;
}

.hero-banner-content .title-container {
  color: #FCFCFC;
  text-align: center;
}

.hero-banner-content .title-container h1 {
  font-weight: 700;
  font-size: 34px;
  line-height: 48px;
  letter-spacing: 1px;
}

.hero-banner-content .title-container h2 {
  font-size: 20px;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: 0.75px;
}

.hero-banner-content .arrow-down-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 3.125rem;
}

.hero-banner-content .arrow-down-container .arrow-down {
  width: 56px;
  height: 56px;
  background-color: #fcfcfc;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  border-radius: 50%;
  cursor: pointer;
  transition: transform 0.5s ease;
}

.hero-banner-content .arrow-down-container .arrow-down:hover {
  transform: scale(1.1);
}

.hero-banner-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.hero-banner-form .get-a-quote {
  background-color: #FCFCFC;
  border-radius: 1.5rem;
  padding: 2rem;
}

.hero-banner-form .get-a-quote .title-container {
  margin-bottom: 38px;
}

.hero-banner-form .get-a-quote .title-container h2 {
  color: #004936;
  letter-spacing: 1px;
  font-weight: 700;
  font-size: 24px;
  line-height: 34px;
}

.hero-banner-form .get-a-quote .information-form {
  margin-top: 1.5rem;
}

.hero-banner-form .get-a-quote .information-form p {
  font-weight: 500;
  font-size: 13px;
  line-height: 22px;
  letter-spacing: 0.25px;
  color: #A0A3BD;
}

@media (min-width: 992px) {
  .hero-banner {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
  }
  .hero-banner_container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    gap: 4rem;
  }
  .hero-banner_container .hero-banner-content {
    margin-bottom: 0;
  }
  .hero-banner_container .hero-banner-content .title-container h1 {
    font-size: 64px;
    font-weight: 700;
    line-height: 88px;
    letter-spacing: 1px;
  }
  .hero-banner-form {
    width: 520px;
    margin: 0 auto;
  }
  .hero-banner-form .get-a-quote .title-container h2 {
    font-size: 40px;
    line-height: 48px;
  }
}
@media (min-width: 1232px) {
  .hero-banner {
    height: 800px;
  }
  .hero-banner_container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-preferred-size: 58%;
        flex-basis: 58%;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
  }
  .hero-banner_container .hero-banner-content {
    max-width: 678px;
  }
}